.Project {
    width: 28rem;
    min-width: 28rem;
    vertical-align: top;
    text-align: center;
    margin-bottom: 2rem;
}

.ProjectBackground {
    height: 45vh;
    width: 100%;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
    background-size: 100%;
}

#desc {
    font-size: 2vh;
    color: rgb(120, 120, 120);
    -webkit-font-smoothing: antialiased;
    line-height: 3vh;
    font-weight: lighter;
    text-align: justify;
}

#title {
    font-size: 1.2rem;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    color: rgb(140, 140, 140);   
}

#image {
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
    height: 20rem;
    border-radius: 0.2rem;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    z-index: 1;
}

#image:hover {
    cursor: pointer;
    -webkit-transform: scale(1.02);
        -ms-transform: scale(1.02);
            transform: scale(1.02);
}


